body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{margin:0;padding:0;box-sizing:border-box}.App{display:flex;flex-direction:column;width:100%;background-color:#050b0f;align-items:center;min-height:100vh}
*{margin:0;padding:0;box-sizing:border-box}.header{font-family:"Inter",sans-serif;background-color:#050b0f;color:#acacac;height:15vh;width:100%;border:none;display:flex;align-items:center;padding:0 10px}.header__logo{max-height:90%;padding-left:5%}.header__title{padding-left:20px}
*{margin:0;padding:0;box-sizing:border-box}.main{display:flex;flex-direction:column;width:100%;align-items:center}@media screen and (min-width: 1200px){.main{flex-direction:row;justify-content:space-between;align-items:flex-start}}
*{margin:0;padding:0;box-sizing:border-box}.nav{width:95%;display:flex;flex-direction:column;align-items:center;background-color:#082032;border:none;border-radius:20px;margin:0 10px 10px 10px;color:#acacac}.nav__heading{margin:10px 0}@media screen and (min-width: 1200px){.nav{height:70vh;width:25%;flex-direction:column;align-items:flex-start;padding-left:10px}}
*{margin:0;padding:0;box-sizing:border-box}.search{color:#050b0f;padding:10px 100px 10px 5px;border:2px solid #ff4c29;border-radius:20px;font-family:"Inter",sans-serif;background-color:#acacac;font-size:1.2rem;max-width:90%}@media screen and (min-width: 1200px){.search{width:90%;font-size:.8rem}}
*{margin:0;padding:0;box-sizing:border-box}@media screen and (min-width: 1200px){.filters-list{margin-top:20px}}
*{margin:0;padding:0;box-sizing:border-box}.abv{display:flex;align-items:center;font-family:"Inter",sans-serif;color:#ff4c29;padding:5px}.abv label{padding-right:5px}.abv input{border:none;background-color:#acacac}
*{margin:0;padding:0;box-sizing:border-box}.classic{display:flex;align-items:center;font-family:"Inter",sans-serif;color:#ff4c29;padding:5px}.classic label{padding-right:5px}.classic input{border:none;background-color:#acacac}
*{margin:0;padding:0;box-sizing:border-box}.acidic{display:flex;align-items:center;font-family:"Inter",sans-serif;color:#ff4c29;padding:5px}.acidic label{padding-right:5px}.acidic input{border:none;background-color:#acacac}
*{margin:0;padding:0;box-sizing:border-box}.beers{width:95%;display:grid;grid-gap:20px;gap:20px;grid-template-columns:1fr;background-color:#082032;border:none;border-radius:20px;margin:0 10px 10px 10px;padding:10px}.beers__cell{display:flex;justify-content:center;align-items:center}@media screen and (min-width: 1200px){.beers{grid-template-columns:1fr 1fr}}
*{margin:0;padding:0;box-sizing:border-box}.beer-tile{display:flex;align-items:center;height:60vh;color:#acacac;width:100%;padding:5px;position:relative;border-top:1px solid #acacac}.beer-tile__left{height:95%;padding:0 5px 0 0;width:40%;display:flex;align-items:flex-start;justify-content:center}.beer-tile__left--image{max-width:100%;max-height:100%}.beer-tile__right{height:95%;display:flex;flex-direction:column;justify-content:space-between;align-items:flex-start;width:55%;position:relative}.beer-tile__right--heading{display:flex;flex-direction:column;justify-content:flex-start;align-items:flex-start;text-align:start}.beer-tile__right--name{color:#ff4c29;font-size:.9rem}.beer-tile__right--tag{font-style:italic;font-size:.9rem}.beer-tile__right--info{display:flex;flex-direction:row;justify-content:space-between;position:relative}.beer-tile__right--info-stats{display:flex;flex-direction:column;align-items:flex-start;justify-content:space-evenly;height:100px}@media screen and (min-width: 768px){.beer-tile__right--name{font-size:1.2rem}.beer-tile__right--tag{font-size:1.2rem}.beer-tile__right--desc{font-size:1.2rem}.beer-tile__right--info-button{position:absolute;right:20px;bottom:10px}}
*{margin:0;padding:0;box-sizing:border-box}.info-button{background-color:#ff4c29;color:#050b0f;border-radius:20px;padding:5px 10px;border:none}.info-button:hover{background-color:#050b0f;color:#ff4c29}
*{margin:0;padding:0;box-sizing:border-box}.beer-modal{display:flex;flex-direction:column;align-items:center;background-color:#082032;color:#acacac;width:100%;position:relative}.beer-modal__close{position:absolute;top:18vh;right:5%}.beer-modal__image{max-height:400px;margin:30px auto}.beer-modal__heading{display:flex;flex-direction:column;justify-content:flex-start;align-items:flex-start;text-align:start;border-top:1px solid #acacac;width:90%;padding:20px 0;position:relative}.beer-modal__heading--name{color:#ff4c29}.beer-modal__heading--tag{font-style:italic}.beer-modal__heading--date{padding:20px 0}.beer-modal__desc{width:90%;margin-bottom:20px}.beer-modal__grid{width:90%;border-top:1px solid #acacac}.beer-modal__info{display:grid;grid-template-columns:1fr 1fr 1fr;grid-gap:5px;gap:5px;margin:20px auto}.beer-modal__info--cell{padding:10px 5px;border-radius:20px;border:1px solid #acacac;color:#ff4c29;text-align:center}.beer-modal__pairings{width:100%;margin-bottom:30px}.beer-modal__pairings h2{padding-left:5%;padding-bottom:20px;color:#ff4c29}.beer-modal__pairings ul{padding-left:10%}.beer-modal__pairings ul li:nth-child(2){padding:10px 0}.beer-modal__brewing{background-color:#050b0f;width:100%}.beer-modal__brewing--heading{color:#ff4c29;padding:20px 0 20px 5%}.beer-modal__brewing--tip{padding-left:10%;font-style:italic}.beer-modal__brewing--ingredients{margin:20px 0 20px 5%;display:flex;justify-content:space-evenly;width:90%;border-top:1px solid #acacac;padding:0 0 20px 5%}.beer-modal__brewing--ingredients-cell{border:1px solid #acacac;margin-top:20px;width:40%;padding:20px}.beer-modal__brewing--ingredients-cell ul{list-style-type:none}.beer-modal__footer{width:100%;background-color:#082032;display:flex;justify-content:center;align-items:center}.beer-modal__footer img{max-width:90%;padding:10px 0}@media screen and (min-width: 768px){.beer-modal__heading--date{position:absolute;top:20px;right:0;padding:0}.beer-modal__info{display:grid;grid-template-columns:1fr 1fr 1fr;grid-gap:10px;gap:10px;margin:20px auto}.beer-modal__info--cell{padding:20px 30px;border-radius:20px;border:1px solid #acacac;color:#ff4c29;text-align:center}}
